form {
    display: flex;
    align-content: center;
    align-items: center;
}
img {
    margin-right: 20px;
}
.search {
    padding: 20px 0;
}
input {
    padding: 10px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    width: 40%;
}
button {
    padding: 10px 15px;
    box-sizing: border-box;
    border: 0;
    margin-left: 5px;
    border-radius: 5px;
    background: #666;
    color: #fff;
}