.movie_item {
    width: 49%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    margin-bottom: 10px;
    background: #fff;
    overflow: hidden;
    cursor: pointer;
    display: flex;
    align-items: center;
}
.movie_image {
    width: 50%;
    max-height: 110px;
    background-color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    // flex-wrap: wrap;
}

.movie_image img {
    width:auto;
    height:auto;
    max-width:100%;
    max-height:100%;
}
.movie_description {
    width: 50%;
    padding: 0 20px;
}
.movie_description_title {
    color: rgb(54, 54, 54);
    font-size: 120%;
}
.movie_description_date {
    color: #666;
    font-size: 90%;
}