/* modal */
.modal-window {
  -webkit-transform: translate(0, 100%);
  transform: translate(0, 100%);
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  opacity: 0;
  line-height: 1.3;
  display: none; }

.modal-window:target {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
  opacity: 1; }

.is-active.modal-window {
  display: block; }

.modal-window:target {
  display: block; }

.modal-window .modal-inner {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 120px;
  left: 50%;
  z-index: 20;
  margin-left: -325px;
  width: 650px;
  min-height: 400px;
  overflow-x: hidden;
  border-radius: 2px;
  background: #fff;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.6); }

.modal-window .modal-inner p {
  padding: 0 20px; }

.modal-window .modal-close {
  display: block;
  text-indent: -100px;
  overflow: hidden; }

.modal-window .modal-close:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10; }

.modal-window .modal-close:after {
  content: '\D7';
  position: absolute;
  top: 125px;
  left: 51%;
  z-index: 20;
  margin-left: 285px;
  background: #fff;
  border-radius: 2px;
  padding: 2px 8px;
  font-size: 1.2em;
  text-decoration: none;
  text-indent: 0; }

.modal-window {
  transition: opacity 0.4s; }

